const camiseta = JSON.parse(localStorage.getItem("camiseta"));
(function(){
    const btnsAddBag = document.querySelectorAll('.release .addToBag');
    btnsAddBag.forEach(function(btnAdd){
        btnAdd.href ="shopbag.html";
        btnAdd.addEventListener('click', (evt) =>{
            let id = btnAdd.parentElement.firstElementChild.dataset.id;
            let nombre = btnAdd.parentElement.firstElementChild.dataset.nombre;
            let newCamiseta = {
                id: id,
                nombre: nombre
            }
            localStorage.setItem("camiseta", JSON.stringify(newCamiseta));
        } )
    });
    let path = window.location.pathname;
    if(path === '/shopbag.html'){
        const img = document.querySelector('.product__img img');
        const webp = document.querySelector('.product__img source'); 
        const product__name = document.querySelector('.product__name');   
        img.src = `build/img/${camiseta.id}.png`;
        webp.srcset = `build/img/${camiseta.id}.webp`;
        product__name.textContent = `Camiseta unisex ${camiseta.nombre}`;
    }
})();