(function(){ 
    let path = window.location.pathname;
    if(path === '/shopbag.html'){
        const btnAdd = document.querySelector('#agregar');
        btnAdd.addEventListener('click', function(evt){
            evt.preventDefault();
            if(verifyInfo()){                
                bolsa.push(articulo);
                localStorage.setItem('bolsa', JSON.stringify(bolsa));
                showCount(bolsa.length);
                sendMessage('Producto agregado a la bolsa', 'exito');
                cleanForm();
            }
        })
        
    }

    function verifyInfo(){
        let vacio = false;
        let tam = '';        
        const tallas = document.querySelectorAll('input[type="radio"]');
        const color = document.querySelector('#color');
        const cant = parseInt(document.querySelector('#cantidad').value);
        tallas.forEach( talla =>{
            if(talla.checked){
                vacio = true;
                tam = talla.value;
            }
        });
        if(!vacio){
            sendMessage('Elige una talla', 'error');
            return false;
        }
        if(color.value === 'none'){
            sendMessage('Elige un color', 'error');
            return false;
        }
        if(cant < 1){
            sendMessage('Elige una cantidad', 'error');
            return false;
        }
        articulo.id = camiseta.id;
        articulo.nombre = camiseta.nombre;
        articulo.talla = tam;
        articulo.color = color.value;
        articulo.cantidad = cant;
        return true;        
    }    

    
    
})();

function showCount(cant){
    let path = window.location.pathname;
    if(path != '/bag.html'){
        let count = document.querySelector('.count');
    if(cant > 0){
        count.textContent = bolsa.length;
        count.style.display = "grid";
    }else{
        count.style.display = "none";
    }
    }
    
}

function sendMessage(message,type){
    let msjAnt = document.querySelector('.mensaje');
    if(msjAnt){
        msjAnt.remove();
    }
    const container = document.querySelector('.details');
    let div = document.createElement('DIV');
    let msj = document.createElement('P');
    div.classList.add('mensaje');
    div.classList.add(`${type}`);
    msj.textContent = `${message}`
    div.appendChild(msj);
    container.appendChild(div);
    setTimeout(() => {
        fadeOut(div);
    }, 1500);
}

function fadeOut(elmt){
    let i = 100;
    setInterval(() => {
        if(i === 0){
            elmt.remove();
            clearInterval();
        }else{
            elmt.style.opacity = i/100;
            i--;
        }
        
    }, 8);
}

function cleanForm(){
    const tallas = document.querySelectorAll('input[type="radio"]');
    const color = document.querySelector('#color');    
    tallas.forEach( talla =>{
        talla.checked = false;
    });
    color.options.item(0).selected = true;
    document.querySelector('#cantidad').value = 0;
}