(function(){
    let path = window.location.pathname;
    if(path === '/bag.html'){
        showBag();
    }   
})();

function showBag(){
        const bolsa_compras = document.querySelector('.bolsa__compras');
        while(bolsa_compras.firstChild){
            bolsa_compras.removeChild(bolsa_compras.firstChild);
        }
        const lista_items = document.createElement('UL');
        let subTotal = 0;
        if(bolsa.length > 0){
            bolsa.forEach(elemento => {
                let item = document.createElement('LI');
                let itemImg = document.createElement('IMG');
                itemImg.src = `build/img/${elemento.id}.png`
                itemImg.alt = `camiseta ${elemento.nombre}`;
                let itemDesc = document.createElement('DIV');
                itemDesc.classList.add('item__info');
                let itemNombre = document.createElement('H3');
                itemNombre.textContent = `Camiseta unisex ${elemento.nombre}`;
                itemDesc.appendChild(itemNombre);
                let itemTalla = document.createElement('P');
                itemTalla.textContent = `Talla: ${elemento.talla}`;
                itemDesc.appendChild(itemTalla);
                let itemColor = document.createElement('P');
                itemColor.textContent = `Color: ${elemento.color}`;
                itemDesc.appendChild(itemColor);
                let cantItem = document.createElement('P');
                cantItem.textContent = `Cantidad: ${elemento.cantidad}`;
                itemDesc.appendChild(cantItem);
                let valItem = document.createElement('P');
                valItem.classList.add('precio');
                let valor = elemento.precio * elemento.cantidad;
                subTotal+= valor;
                valItem.textContent = `Total: $${valor}`;
                itemDesc.appendChild(valItem);
                item.appendChild(itemImg);
                item.appendChild(itemDesc);
                let deleteItem = document.createElement('P');
                deleteItem.innerHTML = '<i class="fas fa-trash"></i>';
                deleteItem.dataset.id = elemento.id;
                deleteItem.setAttribute('onClick', 'deleteItem(this)');
                item.appendChild(deleteItem);
                lista_items.appendChild(item);
                 
            });
            bolsa_compras.appendChild(lista_items);
            let neto = document.createElement('H2');
            neto.textContent = `Tu total a pagar será: $${subTotal}`;
            bolsa_compras.appendChild(neto);
            let btns = document.createElement('DIV');
            btns.classList.add('botones');
            let pagar = document.createElement('A');
            pagar.classList.add('button');
            pagar.classList.add('btn-primary');
            pagar.textContent = "Ir a pagar";
            pagar.href = "404.html"
            btns.appendChild(pagar)
            bolsa_compras.appendChild(btns);
            let continuar = document.createElement('A');
            continuar.classList.add('button');
            continuar.classList.add('btn-primary');
            continuar.textContent = "Seguir mirando";
            continuar.href = "shop.html";
            btns.appendChild(continuar);
            bolsa_compras.appendChild(btns);
        }else{
            const mensaje = document.createElement('P');
            const boton = document.createElement('A');
            mensaje.textContent = "Aún no tienes articulos en tu bolsa. Ve a la tienda y no te pierdas de nuestras ofertas...";
            boton.classList.add('button');
            boton.classList.add('btn-primary');
            boton.textContent = "Ir a la tienda"
            boton.href = "shop.html"
            bolsa_compras.appendChild(mensaje);
            bolsa_compras.appendChild(boton);
        }
}

function deleteItem(obj){
    console.log(`Borrando.. ${obj.dataset.id}`);
    let items = bolsa;
    bolsa = items.filter( item => obj.dataset.id !== item.id);
    localStorage.setItem('bolsa', JSON.stringify(bolsa));
    showBag();
}